export const body = "Caring for your mental health can be a daunting task, especially if you are just starting your care. This site aims to help people find resources, organizational and personal, that will allow them to progress in their individual path towards mental well-being. It is recommended that those with diagnosed mental health disorders explore both \"Daily practices for mental health disorder management\" and \"Mental health practices and info for everyone\", as both groups of resources can be helpful.";